.wrap-qa {
    h1,
    h2,
    p {
        line-height: 1.5em;
    }

    h1 {
        font-weight: 900;
        margin: 96px 0 32px;
    }

    h2 {
        font-weight: 900;
        font-size: 24px;
        margin-bottom: 24px;
    }

    h3 {
        font-weight: 900;
        font-size: 20px;
        margin-bottom: 8px;
    }

    p {
        font-size: 16px;
        margin-bottom: 32px;
    }

    section {
        width: 66.05%;

        &>p:last-child {
            margin-bottom: 80px;
        }
    }
}
@media screen and(max-width:768px) {
    .wrap-qa section {
        width: 100%;
    }
}